  }\n  }\n  @include mobile {\n    display: none;\n  }\n}\n\n.justify-left {\n  @include desktop {\n    justify-content: start;\n  }\n}\n\n.justify-right {\n  @include desktop {\n    justify-content: flex-end;\n  }\n}\n\n.review--posting--comments {\n  padding: 5px;\n  p {\n    margin: 5px;\n  }\n  @include mobile {\n    position: relative;\n  }\n}\n\n.review--posting--content {\n  @include medium_kor;\n  font-size: 14px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n\n.review--posting--commentLength {\n  @include small_kor;\n  color: $darkgrey_medium;\n  &:hover {\n    cursor: pointer;\n  }\n}\n\n.review--posting--createdAt {\n  @include small_kor;\n  color: $darkgrey_light;\n  font-size: 12px;\n}\n\n.review--posting--open_modal {\n  position: absolute;\n  right: 5px;\n  display: flex;\n  align-items: center;\n  span {\n    @include small_kor;\n    color: $darkgrey_light;\n  }\n  img {\n    width: 8px;\n    padding-top: 1px;\n  }\n  &:hover {\n    cursor: pointer;\n    transform: scale(1.1);\n  }\n  @include desktop {\n    bottom: 5px;\n  }\n  @include mobile {\n    bottom: 15px;\n  }\n}\n\n.position-right {\n  @include desktop {\n    left: inherit;\n    right: 10px;\n  }\n}\n\n.footer--empty {\n  @include desktop {\n    margin-bottom: 550px;\n  }\n}\n","@import '@css/fonts.scss';\n\n$basic_width: 700px;\n$desktop: 1024px;\n\n// device mode\n@mixin desktop {\n  @media (min-width: $basic_width) {\n    @content;\n  }\n}\n\n@mixin mobile {\n  @media (max-width: $basic_width) {\n    @content;\n  }\n}\n\n@mixin galaxyFold {\n  @media (max-width: 300px) {\n    @content;\n  }\n}\n\n// utils\n@mixin basic--wrappers {\n  width: $basic_width;\n  margin: 0 auto;\n}\n","// family\n$kor_font_regular: 'Noto Sans KR', sans-serif;\n$eng_font_regular: 'Roboto', sans-serif;\n\n// color\n$darkgrey_darker: #252323;\n$darkgrey_default: rgb(80, 80, 80);\n$darkgrey_medium: rgb(140, 140, 140);\n$darkgrey_light: rgb(184, 184, 184);\n$white: white;\n$lightblue: #73b2ed;\n$pinkred: #ec7063;\n$red_for_submit: #9f062ba3;\n\n//size\n$main_title_22: 22px;\n$sub_title_18: 18px;\n$default_medium_15: 15px;\n$default_small_13: 13px;\n\n// font-set-default\n@mixin medium_kor {\n  font-family: $kor_font_regular;\n  font-size: $default_medium_15;\n  color: $darkgrey_default;\n}\n\n@mixin small_kor {\n  font-family: $kor_font_regular;\n  font-size: $default_small_13;\n  color: $darkgrey_default;\n}\n\n@mixin medium_eng {\n  font-family: $eng_font_regular;\n  font-size: $default_medium_15;\n  color: $darkgrey_default;\n}\n\n@mixin small_eng {\n  font-family: $eng_font_regular;\n  font-size: $default_small_13;\n  color: $darkgrey_default;\n}\n\n// font-set-white\n@mixin big_kor_white {\n  font-family: $kor_font_regular;\n  font-size: $sub_title_18;\n  color: $white;\n}\n\n@mixin medium_kor_white {\n  font-family: $kor_font_regular;\n  font-size: $default_medium_15;\n  color: $white;\n}\n"],"sourceRoot":""}]);
