ents {\n  width: 100%;\n  height: 92%;\n  padding: 20px 15px 0 15px;\n  overflow-y: scroll;\n  &::-webkit-scrollbar {\n    display: none;\n  }\n}\n\n.review--comment--mobile--comment_wrapper {\n  margin-bottom: 30px;\n}\n\n.review--comment--mobile--writter {\n  @include medium_eng;\n  font-weight: bold;\n  margin-right: 7px;\n}\n\n.review--comment--mobile--full_comment {\n  @include medium_kor;\n  white-space: pre-line;\n}\n\n.review--comment--mobile--lowerBox {\n  background-color: white;\n  display: flex;\n  align-items: center;\n  width: 100%;\n  height: 8%;\n}\n\n.review--comment--mobile--form {\n  display: flex;\n  justify-content: space-between;\n  align-content: center;\n  width: 100%;\n  padding: 15px;\n  margin-bottom: 0;\n}\n\n.review--comment--mobile--input {\n  @include medium_kor;\n  width: 90%;\n  border: none;\n  &:focus {\n    outline: none;\n  }\n}\n\n.review--comment--mobile--button {\n  @include medium_kor;\n  color: $lightblue;\n  width: 10%;\n  background: none;\n  padding: 0;\n  border: none;\n  border-radius: 10px;\n  &:hover {\n    cursor: pointer;\n  }\n}\n","@import '@css/fonts.scss';\n\n$basic_width: 700px;\n$desktop: 1024px;\n\n// device mode\n@mixin desktop {\n  @media (min-width: $basic_width) {\n    @content;\n  }\n}\n\n@mixin mobile {\n  @media (max-width: $basic_width) {\n    @content;\n  }\n}\n\n@mixin galaxyFold {\n  @media (max-width: 300px) {\n    @content;\n  }\n}\n\n// utils\n@mixin basic--wrappers {\n  width: $basic_width;\n  margin: 0 auto;\n}\n","// family\n$kor_font_regular: 'Noto Sans KR', sans-serif;\n$eng_font_regular: 'Roboto', sans-serif;\n\n// color\n$darkgrey_darker: #252323;\n$darkgrey_default: rgb(80, 80, 80);\n$darkgrey_medium: rgb(140, 140, 140);\n$darkgrey_light: rgb(184, 184, 184);\n$white: white;\n$lightblue: #73b2ed;\n$pinkred: #ec7063;\n$red_for_submit: #9f062ba3;\n\n//size\n$main_title_22: 22px;\n$sub_title_18: 18px;\n$default_medium_15: 15px;\n$default_small_13: 13px;\n\n// font-set-default\n@mixin medium_